<template>
  <b-container fluid>
    <b-row>
      <b-col>
        <span class="dot">{{ expectedNumber }}</span>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <vue-slider
          class="slider"
          v-bind="options"
          :tooltip="'none'"
          ref="slider"> 
          <template v-slot:dot>
            <svg width="5" height="30">
              <line class="slide-line" x1="0" x2="0" y1="0" y2="30"/>
            </svg>
          </template>
        </vue-slider>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import VueSlider from "vue-slider-component";

export default {
  name: "LineTest",
  props: ["mode", "value"],
  components: {
    VueSlider
  },
  methods: {
    defaultValue() {
      const min = 0;
      const max = this.mode == "100" ? 100 : 1000;
      return Math.random() * (max - min) + min;
    },
    reset() {
      this.options.value = this.defaultValue();
    },
    getValue() {
      return this.$refs.slider.getValue();
    }
  },
  data() {
    return {
      options: {
        min: 0,
        max: this.mode == "100" ? 100 : 1000,
        value: this.defaultValue(),
        marks: [0, this.mode == "100" ? 100 : 1000]
      },
      expectedNumber: this.value
    }
  }
}
</script>

